<template lang="pug">
  
  .settings-app
    .header
      h1.header__title Настройки
    
    .settings-player-info
      .settings-player-info__icon.
        {{playerName[0]}}
      .settings-player-info__player
        h1.settings-player-info__name {{ playerName }}
        p.settings-player-info__phone Мой номер: #[b {{ phoneNumber }}]

    .settings-list
      .settings-list__item
      
        img(src='@/assets/img/icons/disturb.svg').settings-list__icon
        
        .settings-list__info
          p.settings-list__name Не беспокоить

          .settings-list__switch
            input(type='checkbox' v-model='$store.state.settings.noDistrub' @change='distrub' id='checkboxDistrub')
            label(for='checkboxDistrub').settings-list__checkbox

      router-link.settings-list__item(
        v-for='(option, index) in options' 
        :key='index' 
        :to='{name: option.pathName, params: {headerTitle: option.title}}'
      )

        img(:src='require(`@/assets/img/icons/${option.icon}`)').settings-list__icon
        
        .settings-list__info
          p.settings-list__name {{option.title}}

          svg.settings-list__arrow
            use(xlink:href='#arrow')
      


</template>

<script>

  import {mapState} from 'vuex'

  export default {
    computed: mapState({
      playerName: state => state.player.name,
      phoneNumber: state => state.player.phoneNumber,
      noDistrub: state => state.settings.noDistrub,
      options: state => state.settings.settingsOptions
    }),
    methods: {
      distrub() {
        this.noDistrub
          ? this.$alert.show('Режим "Не беспокоить" включен')
          : this.$alert.show('Режим "Не беспокоить" выключен')
      }
    },
    created() {
      this.$store.commit('REFRESH_SYSTEM_STYLE', true)
    }
  }
</script>

<style lang="sass"> @import '../assets/sass/phone/apps/settings/main' </style>